.marquee {
  overflow: hidden; // Hide overflow to create the scrolling effect
  box-sizing: border-box; // Ensure padding and border are included in width
  white-space: nowrap; // Prevent text wrapping

  &__marquee {
    display: inline-flex; // Flex to ensure smooth flow of elements
    white-space: nowrap; // Prevent wrapping
    animation: marquee 30s linear infinite; // Smooth infinite animation
  }

  &__word {
    display: inline-block; // Inline elements for each word
    margin-right: 2rem; // Space between words
    font-size: 1.5rem; // Adjust font size
    color: #333; // Customise text color
  }
}

// Keyframes for the marquee animation
@keyframes marquee {
  from {
    transform: translateX(0); // Start at the initial position
  }
  to {
    transform: translateX(-50%); // Move halfway to loop seamlessly
  }
}
