$off-white: #fdfcf7;
$menu-bg: #b22222;
$menu-text-color: #fff;

.nav {
  min-height: 8vh;
  position: sticky;
  top: 0;
  z-index: 150;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 3rem;
  background-color: $off-white;

  &__logo {
    font-size: 1.8rem;
    font-weight: bold;
  }

  &__menu-icon {
    display: none; // Hidden by default
    cursor: pointer;

    span {
      display: block;
      width: 25px;
      height: 3px;
      margin: 5px auto;
      background-color: #222;
      transition: all 0.3s ease-in-out;
    }
  }

  &__links {
    display: flex;
    gap: 2rem;

    a {
      text-decoration: none;
      color: #222;
      font-size: 1rem;
      transition: color 0.3s ease;

      &:hover {
        color: #fa9c91;
      }
    }
  }

  &__side-menu {
    position: fixed;
    top: 0;
    right: -100%; // Initially hidden off-screen
    height: 100%;
    width: 300px;
    background-color: $menu-bg;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 2rem;
    transition: right 0.3s ease-in-out;

    &--open {
      right: 0; // Slide in when open
    }

    a {
      color: $menu-text-color;
      font-size: 1.5rem;
      margin-bottom: 1.5rem;
      text-decoration: none;

      &:hover {
        color: lighten($menu-text-color, 10%);
      }
    }

    .nav__close {
      position: absolute;
      top: 1rem;
      right: 1rem;
      font-size: 1.5rem;
      cursor: pointer;
      color: $menu-text-color;
    }
  }

  // Responsive styles
  @media (max-width: 768px) {
    &__links {
      display: none; // Hide default links
    }

    &__menu-icon {
      display: block; // Show burger icon
    }

    padding: 0 1rem; // Adjust padding for smaller screens
  }
}
