// Colours
$d-red: #b22222;
$off-white: #fdfcf7;

body {
  margin: 0;
  padding: 0;
  background-color: $off-white;
}

p {
  font-size: 18px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Teko";
  color: #282827;
}

button {
  font-family: "Teko";
}

.text-xl {
  font-size: 6rem;
}
.text-white {
  color: white;
}

h1 {
  font-size: 5.5rem;
  margin: 0;
  padding: 0;
  line-height: 90%;
}

h2 {
  font-size: 3.5rem;
  margin: 0;
  padding: 0;
}

h3 {
  font-size: 2rem;
  margin: 0;
  padding: 0;
}

h4 {
  font-size: 1.5rem;
  margin: 0;
  padding: 0;
}

@media (max-width: 930px) {
  h1 {
    font-size: 4.5rem;
    margin: 0;
    padding: 0;
    line-height: 90%;
  }

  h3 {
    font-size: 1.7rem;
  }
}

@media (max-width: 768px) {
}

.padding {
  padding: 0 2rem;
}

.font-d-red {
  color: $d-red;
}

.main-btn {
  display: inline-block;
  padding: 1rem 1.5rem; // Adjust padding for size
  font-size: 1.5rem; // Adjust font size to match your design
  font-weight: bold; // Keep it bold
  color: $d-red; // Dark red text color
  text-transform: uppercase; // Matches the capitalized text style
  background-color: transparent; // Transparent background
  border: 2px solid $d-red; // Border matches the text color
  cursor: pointer;
  transition: all 0.3s ease; // Smooth hover effect
  box-shadow: none;

  &:hover {
    background-color: $d-red; // Dark red background on hover
    color: #fff; // White text on hover
  }

  @media (max-width: 768px) {
    padding: 0.75rem 1rem; // Adjust padding for size
    font-size: 1.2rem;
  }
}

.main-btn--fill {
  display: inline-block;
  padding: 1rem 1.5rem; // Adjust padding for size
  font-size: 1.5rem; // Adjust font size to match your design
  font-weight: bold; // Keep it bold
  color: white; // Dark red text color
  text-transform: uppercase; // Matches the capitalized text style
  background-color: $d-red; // Transparent background
  border: 2px solid $d-red; // Border matches the text color
  cursor: pointer;
  transition: all 0.3s ease; // Smooth hover effect
  box-shadow: none;

  &:hover {
    background-color: transparent; // Dark red background on hover
    color: $d-red; // White text on hover
  }
}
