.section-two {
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
  align-items: center;
  min-height: 80vh;
  margin: 0 auto;

  &__left,
  &__right {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: left;
    flex: 1;
  }

  &__left {
    background-image: url("../assets/sf-plate-2.jpg");
    background-size: cover;
    background-position: center;
    padding: 40vh 0;
    z-index: 100;
    flex: 1;
  }

  &__image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 70%;
    h1 {
      margin-top: 1rem;
    }
  }

  &__actions {
    margin-top: 2rem;
  }

  // tablet
  @media (max-width: 930px) {
    &__content {
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 100%;
    }

    &__right {
      padding: 1rem;
    }
  }

  // mobile
  @media (max-width: 768px) {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;

    &__content {
      width: 100%;
      padding-top: 10vh;
      padding-bottom: 10vh;
    }

    &__left {
      width: 100%;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
    }
  }
}
