.footer {
  min-height: 30vh;
  background-color: #b22222;
  padding: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;

  &__left {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
  }

  &__right {
    display: flex;
    gap: 2.8rem;
  }

  &__links {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    font-family: "Teko";

    a {
      text-decoration: none;
      color: white;
      font-size: 2rem;
      transition: color 0.3s ease;

      &:hover {
        color: #fa9c91;
      }
    }
  }
}
